@charset "UTF-8";
body {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  background-color: #ffffff;
  box-sizing: border-box;
}

html {
  height: 100%;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.logo {
  display: flex;
  align-items: center;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .logo {
    min-width: 88px;
    margin: 22px 0;
  }
}
@media (min-width: 1440px) {
  .logo {
    margin: 0;
  }
}

.logo__img--mobile {
  display: block;
}
.logo__img--logo-tablet, .logo__img--logo-desktop {
  display: none;
}
@media (min-width: 768px) {
  .logo__img--logo-tablet {
    display: block;
  }
  .logo__img--logo-mobile {
    display: none;
  }
}
@media (min-width: 1440px) {
  .logo__img--logo-desktop {
    display: block;
  }
  .logo__img--logo-mobile, .logo__img--logo-tablet {
    display: none;
  }
}

.logo__description {
  display: none;
}
@media (min-width: 1440px) {
  .logo__description {
    display: block;
    width: 126px;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #333333;
    margin-left: 20px;
  }
}

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-width: 320px;
}
@media (min-width: 768px) {
  .header {
    margin: 0 15px 0 20px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding: 0 40px;
    max-width: 1440px;
    margin: 0 auto;
  }
}

.header__toggle {
  padding: 0;
  width: 60px;
  height: 60px;
  background-image: url("../images/icon-burger.svg");
  background-color: transparent;
  background-position: center center;
  background-repeat: no-repeat;
  cursor: pointer;
  border: none;
}
.header__toggle:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__navigation--nojs .header__toggle {
  display: none;
}

.header__main-nav {
  display: none;
}
@media (min-width: 768px) {
  .header__main-nav {
    display: flex;
    flex-direction: row;
  }
}

@media (max-width: 767px) {
  .header__navigation--nojs .header__main-nav,
.header__navigation--opened .header__main-nav {
    display: flex;
    position: absolute;
    top: 61px;
    flex-direction: column;
    width: 100%;
    align-items: center;
    background-color: #ffffff;
    z-index: 10;
  }
}

.header__navigation--opened .header__toggle {
  background-image: url("../images/icon-cross.svg");
}
@media (max-width: 767px) {
  .header__navigation--opened .header__toggle .header__navigation--closed .header__main-nav {
    display: none;
  }
  .header__navigation--opened .header__toggle .header__navigation--nojs .header__toggle {
    display: none;
  }
  .header__navigation--opened .header__toggle .header__navigation--nojs .header__main-nav {
    display: block;
    position: static;
  }
}

.header__navigation--nojs {
  margin-bottom: 229px;
}

.main-nav__list {
  list-style: none;
  text-transform: uppercase;
  padding: 0;
  width: 100%;
}

.user-menu {
  display: flex;
  flex-wrap: wrap;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  color: #333333;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .page__body {
    min-width: 768px;
  }
}
@media (min-width: 1440px) {
  .page__body {
    min-width: 1440px;
  }
}

.site-list {
  display: flex;
  flex-direction: column;
  margin: 0;
}
@media (min-width: 768px) {
  .site-list {
    max-width: 800px;
    display: flex;
    flex-direction: row;
  }
}

.site-list__item {
  min-height: 57px;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.site-list__item:active {
  background-color: #f0ebff;
}
.site-list__item:first-child::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #f0ebff;
  left: 0;
  top: 0;
}
@media (min-width: 768px) {
  .site-list__item:first-child::before {
    display: none;
  }
}
.site-list__item::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #f0ebff;
  left: 0;
  bottom: 0;
}
@media (min-width: 768px) {
  .site-list__item::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .site-list__item {
    margin-right: 20px;
  }
}

.site-list__link {
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
  color: #333333;
}
.site-list__link:hover, .site-list__link:focus-visible {
  color: #7859cf;
  outline: none;
}
@media (min-width: 768px) {
  .site-list__link {
    padding: 32px 20px;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .site-list__link--active {
    color: #7859cf;
    position: relative;
  }
}
@media (min-width: 768px) {
  .site-list__link--active::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #7859cf;
    left: 0;
    bottom: 0;
  }
}
@media (min-width: 1440px) {
  .site-list__link {
    padding: 30px 20px;
  }
}

.user-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  max-width: 186px;
}
@media (min-width: 1440px) {
  .user-list {
    max-width: 300px;
  }
}

.user-list__item {
  padding: 22px 14px 22px 22px;
}
@media (min-width: 768px) {
  .user-list__item {
    padding: 32px 19px;
  }
}
.user-list__item:active {
  background-color: #f0ebff;
}
@media (min-width: 1440px) {
  .user-list__item {
    padding: 30px 19px;
  }
}

.user-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333333;
}
.user-list__link:hover, .user-list__link:focus-visible {
  color: #7859cf;
  outline: none;
  fill: #7859cf;
}

.user-list__description {
  display: none;
}
@media (min-width: 1440px) {
  .user-list__description {
    display: block;
  }
}

.user-list__icon {
  margin-right: 8px;
}

.slide {
