}
@media (min-width: 768px) {
  .slide {
    position: relative;
    padding: 57px 80px 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media (min-width: 1440px) {
  .slide {
    padding: 180px 80px 242px 45%;
