    align-items: start;
  }
}

.slide__wrapper {
  position: relative;
  max-width: 1440px;
}

.slide__image {
  width: 280px;
  padding: 0;
  margin: 0 0 1px;
}
@media (min-width: 768px) {
  .slide__image {
    width: 476px;
    height: 476px;
    position: absolute;
    top: 295px;
    left: calc(50% - 238px);
    z-index: 5;
  }
}
@media (min-width: 1440px) {
  .slide__image {
    top: -155px;
    left: -623px;
    width: 600px;
    height: 600px;
  }
}

.slide__new {
