  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #7859cf;
}

.slide__title {
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 48px;
  text-align: center;
  letter-spacing: 0.02em;
  padding: 0;
  margin: 0;
  margin-bottom: 10px;
  margin-top: 5px;
}
@media (min-width: 768px) {
  .slide__title {
    font-size: 56px;
