.slide__old-price {
  font-size: 16px;
  line-height: 32px;
  color: #7859cf;
  text-decoration: line-through;
  margin-right: 6px;
}

.slide__new-price {
  font-size: 20px;
  line-height: 32px;
  font-weight: 600;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .slide__new-price {
    margin-right: 20px;
  }
}

.slide__button {
  min-width: 113px;
  max-width: 150px;
  min-height: 40px;
  font-style: normal;
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
  padding: 8px 20px;
  background: #7859cf;
  border-radius: 4px;
  border: none;
}
.slide__button:hover, .slide__button:focus-visible {
  background: #9070ec;
  outline: none;
}
.slide__button:active {
  background: #593cac;
}
.slide__button:disabled {
  background: #bdbdbd;
}

.slider__slide--flat-white {
  background-color: #f3ebe1;
}
@media (min-width: 768px) {
  .slider__slide--flat-white {
    background: linear-gradient(to bottom, #f3ebe1 77%, #ffffff 77% 100%);
  }
}
@media (min-width: 1440px) {
  .slider__slide--flat-white {
    background: none;
    background-color: #f3ebe1;
  }
}

.slider__slide--lavender {
  background-color: #eae6fc;
}
@media (min-width: 768px) {
  .slider__slide--lavender {
    background: linear-gradient(to bottom, #eae6fc 77%, #ffffff 77% 100%);
  }
}
@media (min-width: 1440px) {
  .slider__slide--lavender {
    background: none;
    background-color: #eae6fc;
  }
}

.slider__slide--espresso {
  background-color: #e5e6e8;
}
@media (min-width: 768px) {
  .slider__slide--espresso {
    background: linear-gradient(to bottom, #e5e6e8 77%, #ffffff 77% 100%);
  }
}
@media (min-width: 1440px) {
  .slider__slide--espresso {
    background: none;
    background-color: #e5e6e8;
  }
}

.slider__pagination {
  display: none;
}
@media (min-width: 1440px) {
  .slider__pagination {
    display: block;
  }
}

.slider__button.swiper-button-next,
.slider__button.swiper-button-prev {
  position: absolute;
  top: 18px;
  width: 20px;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  fill: #7859cf;
}
.slider__button.swiper-button-next:hover, .slider__button.swiper-button-next:focus-visible,
.slider__button.swiper-button-prev:hover,
.slider__button.swiper-button-prev:focus-visible {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  outline: none;
}
.slider__button.swiper-button-next:disabled,
.slider__button.swiper-button-prev:disabled {
  fill: #bdbdbd;
}
.slider__button.swiper-button-next:active,
.slider__button.swiper-button-prev:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}
@media (min-width: 768px) {
  .slider__button.swiper-button-next,
.slider__button.swiper-button-prev {
    width: 80px;
  }
}
.slider__button.swiper-button-next::after,
.slider__button.swiper-button-prev::after {
  display: none;
}
@media (min-width: 1440px) {
  .slider__button.swiper-button-next,
.slider__button.swiper-button-prev {
    left: 95%;
  }
}

.slider__button.swiper-button-prev {
  left: 0;
}

.slider__button--next.swiper-button-next {
  right: 0;
  top: 21px;
  transform: scale(-1, 1);
}
@media (min-width: 768px) {
  .slider__button--next.swiper-button-next {
    top: -72px;
  }
}

.slider__button[aria-disabled=true] {
  color: #bdbdbd;
}

.slider__arrow {
  position: relative;
}

.swiper {
  position: relative;
}

.pagination-button {
  position: absolute;
  top: 630px;
  left: 50%;
  display: none;
  width: 202px;
  height: 24px;
}
@media (min-width: 1440px) {
  .pagination-button {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.pagination-button__slider {
  position: relative;
  width: 62px;
  height: 24px;
  margin: 0 8px 0 0;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.pagination-button__slider:last-child {
  margin: 0;
}

.pagination-button__slider::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 64px;
  height: 4px;
  background-color: #ffffff;
}

.pagination-button .pagination-button__slider--current::after {
  background-color: #7859cf;
}

.swiper-pagination-bullet {
  display: none;
}
@media (min-width: 1440px) {
  .swiper-pagination-bullet {
    display: inline-block;
    height: 4px;
    width: 62px;
    background-color: #ffffff;
    margin-left: 8px;
    border-radius: 0;
    opacity: 1;
  }
  .swiper-pagination-bullet--active {
    background-color: #7859cf;
  }
  .swiper-pagination-bullet:hover, .swiper-pagination-bullet:focus-visible {
    outline: none;
    background: rgba(120, 89, 207, 0.25);
  }
  .swiper-pagination-bullet:active {
    background-color: #7859cf;
    opacity: 1;
  }
}

